import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import Aos from 'aos';
import 'aos/dist/aos.css';

const About = () => {
    useEffect(() => {
        Aos.init({ duration:1100 });
    }, []);
    return (
        <div className="contact-container">
        <div className="contact">
        <header className="navbar" data-aos="fade-down">
            <p className="title">MATOREBI</p>
            <div className="right-side">
                <Link to="/contact/" className="btn btn-danger" id="contact-navbar">კონტაქტი</Link>
                <Link to="/about/" id="about-navbar">შესახებ</Link>
                <Link to="/portfolio/" id="portfolio-navbar">პორტფოლიო</Link>
                <p id="number-navbar">+995 551-555-778</p>
            </div>
        </header>  
        <div className="about-header">
        </div>
        <div className="about-header-items" data-aos="fade-right">
            <h1 className="about-title">ვინ ვართ ჩვენ?</h1>
            <p className="about-description">
            ჩვენ ვართ ხადავიკები და მატორისტები ვაკეთებთ მატორებს და სხვა სავალ ნაწილებს იაფად სწრაფად და რაღათქმაუნდა ხარისხიანად. თუ გყავთ მანქანა და გსურთ მისი გარემონტება ან რაიმეს შეცვლა მობრძანდით გლდანის მერვე მიკროში ან გამოგვიძახეთ და ჩვენ მოვალთ.
            </p>
        </div>
        </div>
        </div>
    )
}

export default About;