import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import Aos from 'aos';
import portfolio1 from './assets/portfolio1.jpg'
import portfolio2 from './assets/portfolio2.jpg'
import portfolio3 from './assets/portfolio3.jpg'
import portfolio4 from './assets/portfolio4.jpeg'
import portfolio5 from './assets/portfolio5.jpg'

const Portfolio = () => {
    useEffect(() => {
        Aos.init({ duration:1100 });
    }, []);
    return (
        <div className="portfolio-container">
        <div className="contact">
        <header className="navbar" data-aos="fade-down">
            <p className="title">MATOREBI</p>
            <div className="right-side">
                <Link to="/contact/" className="btn btn-danger" id="contact-navbar">კონტაქტი</Link>
                <Link to="/about/" id="about-navbar">შესახებ</Link>
                <Link to="/portfolio/" id="portfolio-navbar">პორტფოლიო</Link>
                <p id="number-navbar">+995 551-555-778</p>
            </div>
        </header> 
        <div className="about-header"></div>
        <div className="portfolio-header-items-container">
        <div className="portfolio-header-items" data-aos="fade-right">
            <h1 className="portfolio-title">ჩვენი ნამუშევრები</h1>
            <div className="images-portfolio">
            <img src={portfolio1} alt="" id="portfolio-image" />
            <img src={portfolio2} alt="" id="portfolio-image" />
            <img src={portfolio3} alt="" id="portfolio-image" />
            <img src={portfolio4} alt="" id="portfolio-image" />
            <img src={portfolio5} alt="" id="portfolio-image" />
            </div>
        </div>
        </div>
        </div>
        </div>
    )
}

export default Portfolio;