import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Aos from 'aos';
import 'aos/dist/aos.css';
import axios from 'axios';
import successicon from './assets/success.png'

const Contact = () => {
    const [ success, setSuccess ] = useState(false);
    useEffect(() => {
        Aos.init({ duration:1100 });
    }, []);
    const handleSubmit = async(e) => {
        e.preventDefault();
        const name = e.target.name.value;
        const number = e.target.number.value;
        const desc = e.target.desc.value;
        await axios.get(`https://matorebimailer.herokuapp.com/${name}/${number}/${desc}`, {headers: {
            'Access-Control-Allow-Origin' : '*',
            'Access-Control-Allow-Methods':'GET,PUT,POST,DELETE,PATCH,OPTIONS',
            }
        }
        )
        .then(res => {
            if (res) {
                setSuccess(true);
            }
        })
        .catch(eror => {
            if (eror) {
                console.log("ERROR")
            }
        })
    }
    return (
    <div className="contact-container">
    <div className="contact">
        <header className="navbar" data-aos="fade-down">
            <p className="title">MATOREBI</p>
            <div className="right-side">
                <Link to="/contact/" className="btn btn-danger" id="contact-navbar">კონტაქტი</Link>
                <Link to="/about/" id="about-navbar">შესახებ</Link>
                <Link to="/portfolio/" id="portfolio-navbar">პორტფოლიო</Link>
                <p id="number-navbar">+995 551-555-778</p>
            </div>
        </header>
        <div className="contact-header">
            <div className="modal-box-container">
                <div className="modal-box" data-aos="fade-right">
                    <div className="input-fields">
                    {success &&
                    <div className="successbox-container">
                            <div className="successbox">
                                <img src={successicon} alt="" id="successicon"/>
                                <p id="success-text">თქვენი მოთხოვნა წარმატებით გაიგზავნა</p>
                            </div>
                        </div>
                    } 
                        <form onSubmit={handleSubmit} >
                        <input type="text" required className="form-control" id="input-field1" placeholder="თქვენი სახელი..." name="name"/>
                        <input type="number" required className="form-control" id="input-field2" placeholder="თქვენი ნომერი..." name="number"/>
                        <textarea rows = "4" required className="form-control" id="input-field3" placeholder="რა გნებავთ?" name="desc"></textarea>
                        <button type="submit" className="btn btn-primary" id="submit-contact">გაგზავნა</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
    )
}

export default Contact;