import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import მატორი from './assets/მატორი.png';
import ხიდი from './assets/ხიდი.png';
import portfolio1 from './assets/portfolio1.jpg';
import portfolio2 from './assets/portfolio2.jpg';
import portfolio3 from './assets/portfolio3.jpg';
import scrolltotop from './assets/scroll.png';
import Aos from 'aos';
import 'aos/dist/aos.css';


const Home = () => {
    useEffect(() => {
        Aos.init({ duration:1100 });
    }, []);
    return (
    <div className="home-container">
        <div className="home">
            <header className="navbar" data-aos="fade-down">
                <p className="title">MATOREBI</p>
                <div className="right-side">
                    <Link to="/contact/" className="btn btn-danger" id="contact-navbar">კონტაქტი</Link>
                    <Link to="/about/" id="about-navbar">შესახებ</Link>
                    <Link to="/portfolio/" id="portfolio-navbar">პორტფოლიო</Link>
                    <p id="number-navbar">+995 551-555-778</p>
                </div>
            </header>
            <div className="header-box1">
            </div>
            
            <div className="header-box1-container">
            <div className="header-box1-items">
                <img src={მატორი} alt="" id="matori" data-aos="fade-right"/>
                <div id="kontaqti" data-aos="fade-left">
                <p id="title1">
                მატორების შეკეთება, გარემონტება და გაძლიერება მისაღებ ფასად დეტალებზე დაგვიკავშირდით
                </p>
                <Link to="/contact/" className="btn btn-danger" id="contact-header">კონტაქტი</Link>
            </div>
            </div>
            </div>
            <div className="header-box2">
                <div id="vector1">
                    <img src={ხიდი} alt="" id="khidi" data-aos="fade-left" />
                </div>
                <p id="xadaviki-title" data-aos="fade-right" >ჩვენი ხადავიკი გაგირემონტებთ ნებისმიერ სავალ ნაწილს ხიდის კარობკის და ხუნდების ჩათვლით</p>
            </div>
            <div className="header-box3">
                <div id="portfolio-titling-container">
                <div id="portfolio-titling" data-aos="fade-right">
                    <p id="portfolio-titling-text">
                        ნამუშევრების გალერეა
                    </p>
                </div>
                </div>
                <div id="portfolio-images-container-container">
                <div id="portfolio-images-container" data-aos="fade-left">
                    <div id="portfolio1">
                        <img src={portfolio1} alt="" id="portfolio1-image"/>
                        <p id="portfolio1-title">
                            2jz 800HP+ Bi-Turbo
                        </p>
                    </div>
                    <div id="portfolio2">
                        <img src={portfolio2} alt="" id="portfolio2-image"/>
                        <p id="portfolio2-title">
                            M5 E60 Differential
                        </p>
                    </div>
                    <div id="portfolio3">
                        <img src={portfolio3} alt="" id="portfolio3-image"/>
                        <p id="portfolio3-title">
                            C63 AMG Stage 3+
                        </p>
                    </div>
                </div>
                </div>
            </div>
            <img src={scrolltotop} alt="" id="scrollicon" />
        </div>
    </div>
    )
}

export default Home;